<template>
    <div id="chart_ART">
        <!--chart-->
        <div id="myChart" :style="{width:'100%',height:'400px'}"></div>
    </div>
</template>

<script>
    export default {
        name: "chart_ART",
        mounted() {
            console.log(123123);
            this.drawLine();
        },
        methods: {
            drawLine() {
                let myChart = this.$echarts.init(document.getElementById('myChart'), 'shine');
                window.onresize = function () {
                    myChart.resize();
                }
                myChart.setOption({

                    title: {
                        text: '用户score分布',
                        x: '0px',
                        y: '25px',
                        textStyle: {
                            fontSize: 14,
                            color: "#40cc90"
                        }
                    },
                    tooltip: {
                        trigger: 'axis',
                        position: function (p) {   //其中p为当前鼠标的位置
                            return [p[0] - 65, p[1] - 10];
                        }
                    },

                    grid: {
                        left: '3%',
                        top: '15%',
                        containLabel: true
                    },

                    legend: {
                        data: ['当日用户', '前一周用户', '前30天用户']
                    },

                    toolbox: {
                        show: true,
                        feature: {
                            mark: {
                                show: true
                            },
                            dataView: {
                                show: false,
                                readOnly: false
                            },
                            magicType: {
                                show: true,
                                type: ['line', 'bar', 'stack', 'tiled']
                            },
                            restore: {
                                show: true
                            },
                            saveAsImage: {
                                show: true
                            }
                        },
                        x: 100,
                        y: 20
                    },

                    calculable: true,

                    xAxis: [{
                        type: 'category',
                        boundaryGap: false,
                        data: this.xAxis(),
                        axisLabel: {
                            interval: 'auto',
                            rotate: 55
                        },
                    }],

                    yAxis: {type: 'value'},

                    series: []
                });
            },
        }
    }
</script>

<style scoped>

</style>



